import Card from './Card';
const data = [{name: 'Card1',number: 36.8 , text: ""},
{name: 'Card2',number: 23.7 , text: ""},
{name: 'Card3',number: 21.1 , text: ""},
{name: 'Card4',number: 18.4 , text: ""},
{name: 'Card5',number: 15.8 , text: "SEO "},
{name: 'Card6',number: 13.2 , text: "KOL/"},
{name: 'Card7',number: 5.3 , text: ""},
{name: 'Card8',number: 2.6 , text: "Youtube"}]
const CardContainer = ()=>{
    return(
        <div className="cardContainer">
        {data.map((item,index)=><Card key={index} image={item.name} number={item.number} text={item.text} />)}
        </div>
    )
}

export default CardContainer;