import parse from "html-react-parser"
const data = [
    { text: "", backgroundColor: "#F3CE2A" },
    {
        text: "<span>Facebook / Instagram</span><span>Google</span>",
        backgroundColor: "#73C3AE",
    },
    { text: "", backgroundColor: "#E14966" },
    { text: "", backgroundColor: "#F3A727" },
    {
        text: "",
        backgroundColor: "#5BBEDB",
    },
        {
        text: "<span>SEO</span> <span>KOL</span>/",
        backgroundColor: "#E16A9F",
    },
    { text: "<span>Youtube</span>", backgroundColor: "#796DAB" },
];
const List = ({ text, backgroundColor }) => {
    return (
        <div className="list">
            <div
                className="circle"
                style={{ backgroundColor: backgroundColor }}
            ></div>
            <p className="font16px fontWeight600">{parse(text)}</p>
        </div>
    );
};
const PieChartList = () => {
    return (
        <div className="listContainer">
            {data.map((item, index) => (
                <List
                    key={index}
                    text={item.text}
                    backgroundColor={item.backgroundColor}
                />
            ))}
        </div>
    );
};
export default PieChartList;
